//General
import React, { useEffect, useState } from 'react';
import { connect } from 'react-redux';
import {Link} from 'react-router-dom';

//actions
import {getVehicles} from '../actions/vehicles';
import {filterVehicle, applyOrder} from '../actions/models';

//components

//UI
import {Dropdown} from 'react-bootstrap';
import s from '../styles/filters.module.scss';

//code..
function Filters({vehicles, getVehicles, filterVehicle, vehicleId, orderBy, orderDirection, applyOrder}){

    const [state, setState] = useState({
        dropdownToggled: false,
        dropdownToggledFilter: false
    });

    const handleToggle = show => {
        setState({
            ...state,
            dropdownToggled: show
        })
    }

    const handleToggleFilter = show => {
        setState({
            ...state,
            dropdownToggledFilter: show
        })
    }

    const handleVehicleId = (id) => {
        return (e) => {
            console.log(id);
            e.preventDefault();
            filterVehicle(id);
        }
    }

    const handleOrder = (by, direction) => {
        return (e) => {
            e.preventDefault();
            applyOrder({orderBy:by, orderDirection:direction});
        }
    }
   

    useEffect(() => {
        getVehicles();
    },[]);

    return(
        <div className={s['filter-nav']}>
            <Dropdown alignRight className="mr-auto" onToggle={handleToggleFilter}>
                <Dropdown.Toggle as={CustomToggleFilter}>
                        Filtrar por
                        <img src="/ego/img/fill-1.svg" alt="Flecha dropdown"
                             className={s['Fill-1'] + (state.dropdownToggledFilter ? ' ' + s['active'] : '')} />
                </Dropdown.Toggle>

                <Dropdown.Menu >
                    <Dropdown.Item
                        active={vehicleId === null}
                        onClick={handleVehicleId(null)}
                        eventKey="1"
                    >
                        Todos
                    </Dropdown.Item>

                    {vehicles.map((item,index) => (
                        <Dropdown.Item key={item.id}
                            onClick={handleVehicleId(item.id)}
                            active={vehicleId === item.id}
                            eventKey={''+(index+2)}
                        >
                            {item.name}
                        </Dropdown.Item>
                    ))}
                    
                </Dropdown.Menu>
            </Dropdown>


            <div className={s['desktop-filter']}>
                <span className={s['filter-title']}>
                    Filtrar por
                </span>
                <Link to="#" onClick={handleVehicleId(null)} className={s['filter-badge'] + (vehicleId === null ? ' ' + s['active'] : '')}>
                    Todos
                </Link>
                {vehicles.map(item => (
                    <Link key={item.id} onClick={handleVehicleId(item.id)} to="#" className={s['filter-badge'] + (vehicleId === item.id ? ' '+s['active'] : '')}>
                        {item.name}
                    </Link>
                ))}
            </div>

            <div className="w-25 text-right">
            <Dropdown alignRight className="ml-auto" onToggle={handleToggle}>
                <Dropdown.Toggle as={CustomToggle}>
                        Ordernar por
                        <img src="/ego/img/fill-1.svg" alt="Flecha dropdown"
                             className={s['Fill-1'] + (state.dropdownToggled ? ' ' + s['active'] : '')} />
                </Dropdown.Toggle>

                <Dropdown.Menu >
                    <Dropdown.Item
                        active={orderBy === 'id' && orderDirection === 'asc'}
                        onClick={handleOrder('id','asc')}
                        eventKey="1"
                    >
                        Nada
                    </Dropdown.Item>
                    <Dropdown.Item
                        active={orderBy === 'price' && orderDirection === 'asc'}
                        onClick={handleOrder('price','asc')}
                        eventKey="2"
                    >
                        De <b>menor</b> a <b>mayor</b> precio
                    </Dropdown.Item>
                    <Dropdown.Item
                        active={orderBy === 'price' && orderDirection === 'desc'}
                        onClick={handleOrder('price','desc')}
                        eventKey="3"
                    >
                        De <b>mayor</b> a <b>menor</b> precio
                    </Dropdown.Item>
                    <Dropdown.Item
                        active={orderBy === 'year' && orderDirection === 'desc'}
                        onClick={handleOrder('year','desc')}
                        eventKey="3"
                    >
                        Más <b>nuevos</b> primero
                    </Dropdown.Item>
                    <Dropdown.Item
                        active={orderBy === 'year' && orderDirection === 'asc'}
                        onClick={handleOrder('year','asc')}
                        eventKey="3"
                    >
                        Más <b>viejos</b> primero
                    </Dropdown.Item>
                </Dropdown.Menu>
            </Dropdown>
            </div>
        </div>
    );
}


const CustomToggle = React.forwardRef(({ children, onClick }, ref) => (
    <a
        className={s['filter-title'] + ' ' + s['filter-title-right']}
        href="#"
        ref={ref}
        onClick={(e) => {
        e.preventDefault();
        onClick(e);
        }}
    >
        {children}
    </a>
));


const CustomToggleFilter = React.forwardRef(({ children, onClick }, ref) => (
    <a
        className={s['filter-title'] + ' ' + s['filter-title-left']}
        href="#"
        ref={ref}
        onClick={(e) => {
        e.preventDefault();
        onClick(e);
        }}
    >
        {children}
    </a>
));

function mapStateToProps(state){
    return {
        orderBy: state.models.orderBy,
        orderDirection: state.models.orderDirection,
        vehicleId: state.models.vehicleId,
        vehicles: state.vehicles.vehicles
    }
}

function mapDispatchToProps(dispatch){
    return {
        getVehicles: () => dispatch(getVehicles()),
        filterVehicle: id => dispatch(filterVehicle(id)),
        applyOrder: data => dispatch(applyOrder(data))
    }
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(Filters);